import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App
})
app.$mount()

// 针对小程序setData方法判断
Vue.prototype.setData = Vue.prototype.setData || function () {
  const data = arguments[0] || {}
  for(let key in data) {
    this[key] = null
    setTimeout(()=>{
      this[key] = data[key]
    }, 0)
  }
}